import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  actualizarEstadoGuia,
  updateitemListDebaja,
  findGuia,
  currentGuiaDetail,
} from "../redux/actions/guias_actions";

import { useToasts } from "react-toast-notifications";
import {
  convertDate,
  removeSpaces,
  savePDF,
  validarEmail,
} from "../helpers/helper";

export const UseFormDardeBajaGuias = () => {
  const dispatch = useDispatch();
  const {
    guias: { listGuias, fetching, msgError, printGuia },
    auth: { dataProfile },
  } = useSelector((state) => state);
  const [values, setValues] = useState({
    BÃºsqueda: "guia",
    textSearch: "",
    shoDetail: false,
    showDownload: false,
    advanceSearch: false,
  });
  const rolUser =
    dataProfile?.rol === "admin"
      ? "Administrador"
      : dataProfile?.rol === "puntoVenta"
      ? "Punto de venta"
      : dataProfile?.rol === "sucursal"
      ? "Sucursal"
      : "Corporativo";
  const [CurrentGuia, setCurrentGuia] = useState({});

  //mensajes de alerta
  const { addToast } = useToasts();

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
      autoSearch: false,
    });
  };
  const [autoSearch, setautoSearch] = useState(false);
  const onclickSwitch = () => {
    setautoSearch(!autoSearch);
  };

  const handlerChangeText = (e) => {
    e?.preventDefault();
    console.log(e.target.value);
    let res =
      e.target.name === "provincia"
        ? e.target.value
        : e.target.value.replace(/ /g, "");
    setValues({
      ...values,
      [e.target.name]: res.trim(),
    });
  };

  const onPressEnter = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    if (e.key === "Enter") {
      // console.log("pres enter");
      // getGuiaSearch();
    }
  };
  const motivohandleChange = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const getGuiaSearch = () => {
    const errorMesajje = () => toastSms(`${msgError}`, "error");
    const noEncontrada = () => toastSms(`Guia no encontrada`, "info");
    if (values?.advanceSearch === true) {
      if (
        values.startDate === undefined ||
        values.endDate === undefined ||
        values.provincia === undefined
      ) {
        toastSms(
          ` Debes ingresar todos los datos para realizar la bÃºsqueda: Fecha Inicial, Fecha Final y  Provincia.`,
          "error"
        );
      } else {
        dispatch(
          findGuia(
            values?.BÃºsqueda,
            values?.textSearch,
            errorMesajje,
            noEncontrada,
            convertDate(values?.startDate),
            convertDate(values?.endDate),
            values.provincia
          )
        );
      }
    } else {
      if (values?.textSearch.trim()) {
        dispatch(
          findGuia(
            values?.BÃºsqueda,
            values?.textSearch,
            errorMesajje,
            noEncontrada
          )
        );
      } else {
        toastSms(`El campo de BÃºsqueda esta vacio`, "error");
      }
    }
  };
  const getGuiaSearchAuto = (idGuia) => {
    // console.log(idGuia);
    const errorMesajje = () => toastSms(`${msgError}`, "error");
    const noEncontrada = () => toastSms(`Guia no encontrada`, "info");
    if (idGuia.trim()) {
      dispatch(
        findGuia(
          values?.BÃºsqueda,
          removeSpaces(idGuia),
          errorMesajje,
          noEncontrada
        )
      );
    } else {
      toastSms(`El campo de BÃºsqueda esta vacio`, "error");
    }
  };

  const showDetails = (status, guia) => {
    if (guia != null) {
      // console.log("contiene guia");
      setValues({
        ...values,
        shoDetail: status,
      });
      setCurrentGuia(guia);
    } else {
      setValues({
        ...values,
        shoDetail: status,
      });
      setCurrentGuia({});
    }
  };

  const darDeBaja = () => {
    const errorMessaje = () => toastSms(`${msgError}`, "error");
    const Success = () => toastSms(`Guia actualizada con exito`, "success");

    let updateStadoGuia = {
      ...CurrentGuia,
      datosBaja: values,
      estado: "de baja",
    };
    dispatch(actualizarEstadoGuia(updateStadoGuia, Success, errorMessaje));
  };
  const solicitarAnulacion = () => {
    // console.log("dentro de solicitar guia");
    const errorMessaje = () => toastSms(`${msgError}`, "error");
    const Success = () => toastSms(`Guia actualizada con exito`, "success");

    let updateStadoGuia = {
      ...CurrentGuia,
      estado: "pendiente anulacion",
      anulacion: {
        motivo: values?.motivoAnulacion,
        usuario: dataProfile?.email,
        rol: rolUser?.toString(),
        fecha: Date.now(),
      },
    };
    dispatch(actualizarEstadoGuia(updateStadoGuia, Success, errorMessaje));
    dispatch(
      updateitemListDebaja(updateStadoGuia, updateStadoGuia.idGuia, listGuias)
    );
    // showDetails(false, null);
    clearMotivo();
  };

  const clearMotivo = () => {
    setValues({
      ...values,
      BÃºsqueda: "guia",
      textSearch: "",
      shoDetail: false,
    });
  };

  const dowloadGuia = (guia) => {
    setValues({
      ...values,
      showDownload: true,
    });
    setCurrentGuia(guia);
    // console.log(CurrentGuia);
    savePDF("current-guia-salve");
    setTimeout(() => {
      setValues({
        ...values,
        showDownload: false,
      });
    }, 2000);
  };
  const printGuiaAction = (guia) => {
    setCurrentGuia(guia);
    // console.log(CurrentGuia);
    setTimeout(() => {
      window.print();
    }, 500);
  };
  const setAdvanceSearch = () => {
    console.log(values.autoSearch);
    setValues({
      ...values,
      advanceSearch: !values.advanceSearch,
    });
    if (autoSearch === true) {
      setautoSearch(false);
    }
  };
  return [
    values,
    fetching,
    listGuias,
    CurrentGuia,
    autoSearch,
    onclickSwitch,
    setAdvanceSearch,
    darDeBaja,
    getGuiaSearchAuto,
    solicitarAnulacion,
    handlerChangeText,
    getGuiaSearch,
    showDetails,
    dowloadGuia,
    printGuiaAction,
    motivohandleChange,
    onPressEnter,
  ];
};
