import html2pdf from "html2pdf.js";
import { v4 as uuid } from "uuid";

export const ParseDate = (miliseconds) => {
  const date = new Date(Number.parseInt(miliseconds));
  const formatDate = `${
    date.getDate() >= 10 ? date.getDate() : `0${date.getDate()}`
  }/${
    date.getMonth() + 1 >= 10 ? date.getMonth() + 1 : `0${date.getMonth() + 1}`
  }/${date.getFullYear()}      ${
    date.getHours() >= 10 ? date.getHours() : `0${date.getHours()}`
  }H${date.getMinutes() >= 10 ? date.getMinutes() : `0${date.getMinutes()}`}`;

  return formatDate;
};
export const validarEmail = (email) => {
  let emailRegex = /^[-\w.%+]{1,64}@(?:[A-Z0-9-]{1,63}\.){1,125}[A-Z]{2,63}$/i;
  return emailRegex.test(email);
};

export function truncate(num, places) {
  return Math.trunc(num * Math.pow(10, places)) / Math.pow(10, places);
}

export function generateGuiaNumber(guiaID) {
  let numberguia = "1000000000";
  let numberid = guiaID.toString().length;
  var xStr = numberguia.substring(0, numberguia.length - numberid);
  var newString = xStr + guiaID;
  return newString;
}

export function savePDF(idSalve) {
  try {
    console.log("dentro de la imprecion");
    var content = document.getElementById(`${idSalve}`);
    let currentDate = ParseDate(Date.now().toString().trim());
   return  html2pdf(content, {
      filename: `guia_${currentDate}.pdf`,
      image: { type: 'jpeg', quality: 1 },
      margin: [5, 5, 5, 5],
    });
  } catch (error) {
    console.log(`error pdf: ${error}`);
    return;
  }
}
export function generateId() {
  let res = uuid().toString();
  let idshort = res.substring(0, 11);
  let restem = idshort.replace(/ /g, "");
  return restem.toString().toUpperCase();
}

export function removeSpaces(StringRmv) {
  let res = StringRmv.replace(/ /g, "");
  return res;
}

export function returnTotalGuias(pesos, numPiezas) {
  let totalpesoguias = 0;
  if (numPiezas > 1) {
    pesos.map((e) => {
      totalpesoguias = parseFloat(e.pesototal) + totalpesoguias;
    });
  } else {
    totalpesoguias = pesos[0].pesototal;
  }
  return totalpesoguias;
}
export function returnTotalValorAsegurado(pesos, numPiezas) {
  let totalpesoguias = 0;
  if (numPiezas > 1) {
    pesos.map((e) => {
      totalpesoguias = parseFloat(e.v_asegurado ?? 0) + totalpesoguias;
    });
  } else {
    totalpesoguias = pesos[0].v_asegurado ?? 0.0;
  }
  return parseFloat(totalpesoguias);
}

export function convertDate(date) {
  var myDate = new Date(`${date}T00:00:11+0000`);
  var result = myDate.getTime();
  console.log(result);
  return result;
}
