import React, { useState } from "react";
import "../login/login.css";
import { Link } from "react-router-dom";
import { useFormLogin } from "../../hooks/use_formLogin";
import { useSelector } from "react-redux";
import Spinner from "../spinner/spinner";

function Login() {
  const {
    auth: { fetching },
  } = useSelector((state) => state);
  const [showPass, setShowPass] = useState(false);

  const [values, , handleInputChange, onClickChangeHandler, handlerBlurEmail] =
    useFormLogin({
      email: "",
      password: "",
      errorEmail: "",
      errorMsg: "",
    });
  const { email, password, errorEmail } = values;

  const visiblePassAction = () => {
    return setShowPass(!showPass);
  };

  return (
    <div className="body">
      <div className="container ">
        <form className="form-signin">
          <div className="img mb-4"></div>

          <p className=" text-muted">
            Bienvenido al Sistema Operativo ElectrÃ³nico <strong>IRIS</strong>,
            ingresa tus datos para acceder a tu panel administrativo.
          </p>
          <label  className="sr-only">
            Email/Usuario
          </label>
          <div className="">
            <div className="mb-3 input-group">
              <label htmlFor="simpleinput" className="form-label-input">
                Usuario
              </label>
              <input
                type="text"
                className="form-control input-user"
                placeholder="Email / Usuario"
                aria-label=""
                aria-describedby="basic-addon1"
                name="email"
                value={email}
                onChange={handleInputChange}
                onBlurCapture={handlerBlurEmail}
              />
               <div className="input-group-append">
                    <span className="input-group-text" id="basic-addon2">
                      @rapifas.com
                    </span>
                  </div>
              {errorEmail !== "" ? (
                <div className="w-100 text-left">
                  <p className="text-error-red">{errorEmail}</p>
                </div>
              ) : (
                <></>
              )}
            </div>
          </div>
          <div className="mb-3 input-group">
            <label  className="form-label-input">
              ContraseÃ±a
            </label>
            <input
              type={showPass ? "text" : "password"}
              className="form-control"
              placeholder="ContraseÃ±a"
              aria-label="Recipient's username"
              aria-describedby="basic-addon2"
              value={password}
              name="password"
              onChange={handleInputChange}
            />
            <div className="input-group-append">
              <span className="input-group-text" id="basic-addon2">
                <div className="btn-action-view" onClick={visiblePassAction}>
                  {showPass === true ? (
                    <i className="material-icons">visibility</i>
                  ) : (
                    <i className="material-icons">visibility_off</i>
                  )}
                </div>
              </span>
            </div>
          </div>

          {/* <div className="checkbox mb-3 text-start">
            <label>
              <input
                type="checkbox"
                checked={showPass}
                onClick={() => setShowPass(!showPass)}
              />{" "}
              Visualizar
            </label>
          </div> */}

          {/* <Link to='/dashboard/crearGuia'> */}
          {fetching ? (
            <div className="w-100 d-flex justify-content-center ">
              <Spinner />
            </div>
          ) : (
            <button
              className="btn  btn-primary  w-100"
              onClickCapture={(e) => onClickChangeHandler(e)}
            >
              Ingresar
            </button>
          )}

          {/* </Link> */}
          <a href="/" className="btn-back">
            <p className="mt-3 mb-3 text-muted">Regresar</p>
          </a>
        </form>
      </div>
    </div>
  );
}

export default Login;
